import React, { useEffect, useState } from "react"
import { Section, Heading, Loading, Empty } from "components"
import { Row, Col, Input, message } from "antd"
import { Link } from "react-router-dom"
import PlaceCard from "./PlaceCard"
import { connect } from "react-redux"
import { fetchPlaceTypes, searchPlaces, fetchNearbyPlaces } from "store/actions/placesActions"
import styled from "styled-components"
import { motion } from "framer-motion"
import CategoryList from "components/common/CategoryList"
import { country } from "App"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { useGeolocation } from "react-use"
import useInternational from "helpers/hooks/useInternational"

const FilterPanel = styled(motion.div)`
	margin-bottom: 1em;
	border-radius: 5px;
	padding: 1em 0;
	.right {
		text-align: right;
	}
`

const StyledInput = styled(Input.Search)`
	&& {
		margin-bottom: 1em;
		display: block;
	}
`

const filterPanelVariants = {
	visible: { opacity: 1, y: 0, display: "block", transition: { staggerChildren: 0.2 } },
	hidden: { opacity: 0, y: -300, display: "none" }
}

function Places({ loading, fetchPlaceTypes, placeTypeOptions, searchPlaces, places, i18n, fetchNearbyPlaces }) {
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}

	const [filterValues, setFilterValues] = useState({ type: 12, keyword: "" })
	const { latitude = "", longitude = "" } = useGeolocation()
	const { countryCode = "" } = useInternational()

	const renderPlaces = () => {
		if (loading)
			return (
				<Row>
					<Col lg={12}>
						<Loading />
					</Col>
				</Row>
			)

		if (places.length === 0)
			return (
				<Section centered>
					<Empty description={<Trans>Oops! No data found for now</Trans>} />
				</Section>
			)

		return (
			<Row gutter={24}>
				{(places || []).map((item) => {
					const name = item.name.split(" ").join("-")
					return (
						<Col lg={6} xs={12} key={item.pvid} style={{ marginBottom: "2em" }}>
							<Link to={`/${country}/study_places/places/${name}--${item.pvid}`}>
								<PlaceCard css="cursor: pointer" data={item} />
							</Link>
						</Col>
					)
				})}
			</Row>
		)
	}

	const handleSearch = (keyword) => {
		setFilterValues({ ...filterValues, keyword })
		searchPlaces(keyword, -1, country, Number(userDetails.pvid) || 0)
	}

	const handleSelectType = (type) => {
		setFilterValues({ ...filterValues, keyword: "", type })
		fetchNearbyPlaces({ country, lat: latitude, lng: longitude, userPvid: userDetails.pvid || 0 }, type, countryCode)
	}

	useEffect(() => {
		fetchPlaceTypes(country)

		const handleFetchNearby = () => {
			fetchNearbyPlaces({ country, lat: latitude, lng: longitude, userPvid: userDetails.pvid || 0 }, filterValues.type, countryCode)
		}

		if (!filterValues.keyword) handleFetchNearby()

		return () => message.destroy()
	}, [
		fetchPlaceTypes,
		latitude,
		longitude,
		fetchNearbyPlaces,
		searchPlaces,
		filterValues.type,
		userDetails.country,
		filterValues.keyword,
		userDetails.pvid,
		countryCode
	])

	return (
		<Section>
			<FilterPanel variants={filterPanelVariants} animate="visible" initial="hidden" transition={{ delay: 2 }}>
				<Row type="flex" justify="space-between">
					<Col lg={8} className="right">
						<StyledInput
							name="keyword"
							placeholder={i18n._(t`E.g. Study places in Bandung`)}
							onSearch={handleSearch}
							allowClear
						/>
					</Col>
				</Row>
			</FilterPanel>{" "}
			<Heading
				bold
				level={4}
				marginBottom="1em"
				content={<Trans>Places to study</Trans>}
				subheader={<Trans>School is not the only place to study. The list below proves it</Trans>}
			/>
			<CategoryList options={placeTypeOptions} onClick={handleSelectType} type={filterValues.type} />
			{renderPlaces()}
		</Section>
	)
}

const mapState = ({ place }) => {
	const places = place.placesError === 404 ? [] : place.places
	const placeTypeOptions = (place.placeTypes || []).map(({ pvid, label }) => ({
		// value: userDetails.country === "sg" ? pvid : /\s/.test(name) ? name.split(" ").join("_") : name,
		value: pvid,
		label
	}))

	return {
		places,
		placeTypeOptions,
		isDifferentCountry: place.isDifferentCountry,
		placesError: place.placesError,
		loading: place.loading
	}
}

export default withI18n()(connect(mapState, { fetchPlaceTypes, searchPlaces, fetchNearbyPlaces })(Places))
